import './MyLabel.css';

export interface MyLabelProps {
	/**
	 * Este es el defecto del texto
	 */
	label: string;
	/**
	 * TamaÃ±o por defecto del texto
	 */
	size: 'normal' | 'h1' | 'h2' | 'h3';
}

export const MyLabel = ({
	label = 'No Label',

	size = 'normal',
}: MyLabelProps) => {
	return <span className={`${size}`}>{label}</span>;
};
